<template>
  <div :class="[
    'mtui-input',
    {
      'is-disabled': disabled,
      'is-readonly': readonly
    }
  ]" v-if="type !== 'textarea'">
    <input
      :type="type"
      :value="value"
      :name="name"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :maxlength="maxlength"
      @input="inputHandler"
      @change="changeHandler"
      @focus="focusHandler"
      @blur="blurHandler"
    >
    <m-icon class="mtui-input-clear" type="close-circled" v-if="clearable && !disabled && !readonly" v-show="value.length > 0" @click.native="clearHandler"></m-icon>
    <slot></slot>
  </div>

  <div :class="[
    'mtui-textarea',
    {
      'is-disabled': disabled,
      'is-readonly': readonly
    }
  ]" v-else>
    <textarea
      :value="value"
      :name="name"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :rows="row"
      :maxlength="limit"
      @input="inputHandler"
      @change="changeHandler"
      @focus="focusHandler"
      @blur="blurHandler"
    >
    </textarea>
    <div class="mtui-textarea-counter">{{ value.length }}/{{ limit }}</div>
  </div>
</template>

<script>
import Icon from '../icon/Icon'

export default {
  name: 'm-input',
  components: { MIcon: Icon },
  props: {
    type: { // input['text', 'password', 'search', 'number', 'file']
      type: String,
      default: 'text'
    },
    value: { // 
      type: [String, Number],
      default: ''
    },
    name: String, // inputname
    placeholder: String, // 
    disabled: Boolean, // 
    readonly: Boolean, // 
    maxlength: String, // 
    clearable: { // 
      type: Boolean,
      default: false
    },
    row: { // textarea
      type: Number,
      default: 3
    },
    limit: { // textarea
      type: Number,
      default: 200
    }
  },
  methods: {
    // 
    clearHandler () {
      this.$emit('input', '')
      this.$emit('clear')
    },

    inputHandler (event) {
      this.$emit('input', event.target.value)
    },

    changeHandler (event) {
      this.$emit('change', event.target.value)
    },

    focusHandler () {
      this.$emit('focus')
    },

    blurHandler () {
      this.$emit('blur')
    }
  }
}
</script>

<style lang="scss" scoped>
.mtui-input {
  @extend .mtui-common;
  @include flex-center();
  width: 100%;
  background-color: white;
  border-radius: 4px;

  &.is-disabled {
    background-color: $colorBg;
    cursor: not-allowed;

    input {
      color: lighten($black, 60%);
    }
  }

  &.is-readonly {
    input {
      color: lighten($black, 60%);
    }
  }

  input {
    flex: 1;
    padding: 5px 8px;
    min-width: 100px;
    height: 24px;
    background-color: transparent;
    border: 0;
    font-size: $fontXMedium;
    color: lighten($black, 20%);
    -webkit-tap-highlight-color: transparent;
    outline: none;
    &::-webkit-input-placeholder {
      color: lighten($black, 60%);
    }
  }

  &-clear {
    display: inline-block;
    width: 34px;
    line-height:  34px;
    text-align: center;
    font-size: $fontLarge;
    color: lighten($black, 60%);
  }
}

.mtui-textarea {
  @extend .mtui-input;
  padding: 6px 8px;
  height: auto;
  flex-direction: column;

  &.is-disabled {
    textarea {
      color: lighten($black, 60%);
    }
  }

  &.is-readonly {
    textarea {
      color: lighten($black, 60%);
    }
  }

  textarea {
    padding: 0;
    width: 100%;
    background-color: transparent;
    border: 0;
    font-size: $fontXMedium;
    font-family: $font;
    color: lighten($black, 20%);
    outline: none;
    resize: none;
    &::-webkit-input-placeholder {
      color: lighten($black, 60%);
    }
  }

  &-counter {
    flex: 1;
    box-sizing: border-box;
    width: 100%;
    text-align: right;
    line-height: 1.6;
    color: lighten($black, 60%);
  }
}
</style>
