<template>
  <m-dialog v-model="showFlag" :closeOnClickMask="false" :showCloseButton="closeable">
    <!--  -->
    <template slot="title" v-if="title !== ''">{{ title }}</template>

    <!--  -->
    <div>{{ message }}</div>

    <!--  -->
    <template slot="foot">
      <m-button inline text no-radius @click="cancel">{{ cancelTxt }}</m-button>
      <m-button inline text type="minor" no-radius @click="confirm">{{ confirmTxt }}</m-button>
    </template>
  </m-dialog>
</template>

<script>
export default {
  name: 'm-confirm',
  data () {
    return {
      showFlag: false,
      title: '',
      message: '',
      cancelTxt: '',
      confirmTxt: '',
      onCancel: null,
      onConfirm: null,
      closeable: false
    }
  },
  watch: {
    showFlag (val) {
      if (!val) {
        this.dataReset()
      }
    }
  },
  methods: {
    hide () {
      this.showFlag = false
    },

    show () {
      this.showFlag = true
    },

    confirm () {
      this.onConfirm && this.onConfirm()
      this.hide()
    },

    cancel () {
      this.onCancel && this.onCancel()
      this.hide()
    },

    // 
    dataReset () {
      this.title = ''
      this.message = ''
      this.cancelTxt = ''
      this.confirmTxt = ''
      this.onCancel = null
      this.onConfirm = null
      this.closeable = false
    }
  }
}
</script>

<style lang="scss" scoped>
.mtui-dialog {
  z-index: 980;

  .mtui-dialog-foot .mtui-btn {
    position: relative;
    flex: 1;
    &:not(:first-child)::after {
      @include border-left-line();
      top: 50%;
      transform: scale(.5, .5) translateY(-50%);
    }
  }
}
</style>
