<template>
  <m-dialog v-model="showFlag" :closeOnClickMask="false" :showCloseButton="closeable">
    <!--  -->
    <template slot="title" v-if="title !== ''">{{ title }}</template>

    <!--  -->
    <div>{{ message }}</div>

    <!--  -->
    <m-button type="minor" text no-radius @click="alert" slot="foot">{{ confirmTxt }}</m-button>
  </m-dialog>
</template>

<script>
import Dialog from '../dialog/Dialog'
import Button from '../button/Button'

export default {
  name: 'm-alert',
  components: {
    MDialog: Dialog,
    MButton: Button
  },
  data () {
    return {
      showFlag: false,
      title: '',
      message: '',
      confirmTxt: '',
      onConfirm: null,
      closeable: false
    }
  },
  watch: {
    showFlag (val) {
      if (!val) {
        this.dataReset()
      }
    }
  },
  methods: {
    hide () {
      this.showFlag = false
    },

    show () {
      this.showFlag = true
    },

    alert () {
      this.onConfirm && this.onConfirm()
      this.hide()
    },

    // 
    dataReset () {
      this.title = ''
      this.message = ''
      this.confirmTxt = ''
      this.onConfirm = null
      this.closeable = false
    }
  }
}
</script>

<style lang="scss" scoped>
.mtui-dialog {
  z-index: 990;
}
</style>
